<template>
  <div>
    <v-h1 default-back-to="/legal/copyright-infringements">Submit legal claim</v-h1>

    <p class="mb-2">We take copyright infringement seriously. We ask you to help us to ensure we address it promptly and effectively.</p>

    <p class="mb-2">Please make sure that your claim includes:</p>
    <ul class="mb-5 list-inside list-disc">
      <li>A permission for appdb to send you link to verify your identity (once) in order to complete claim submission process;</li>
      <li>
        A statement that you have a good faith belief that use of the material in the manner complained of is not authorized by the copyright owner,
        its agent, or the law; and
      </li>
      <li>
        A statement that, under penalty of perjury, the information in the claim is accurate and you are authorized to act on behalf of the owner of
        the exclusive right that is allegedly infringed
      </li>
    </ul>

    <form
      class="flex flex-col gap-5"
      @submit.prevent="submit"
    >
      <v-label label="URL where infringement content is located">
        <v-input
          v-model="form.url"
          type="url"
        />
      </v-label>

      <v-label label="Claim text">
        <v-textarea
          v-model="form.license_text"
          :rows="3"
        />
      </v-label>

      <v-button
        class="self-start"
        variant="primary"
        :loading="isSubmitting"
      >
        Submit
      </v-button>
    </form>
  </div>
</template>

<script setup lang="ts">
import type { Payload } from "core/types/api"

definePageMeta({
  requiredRole: {
    name: "user",
    redirect: "/copyright-claim",
  },
})

const form = reactive<Payload<typeof submitCopyrightClaim>>({
  url: "",
  license_text: "",
})

const isSubmitting = ref(false)
async function submit() {
  isSubmitting.value = true

  try {
    await submitCopyrightClaim(form)

    const { notifySuccess } = useNotification()
    notifySuccess("Copyright claim was submitted")
    navigateTo("/legal/copyright-infringements")
  } finally {
    isSubmitting.value = false
  }
}
</script>
